@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700&display=swap');

:root {
  --bg-color: #282D35;
  --nav-color: #21222A;
  --fade-anim: fade 1s forwards;
}

.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: var(--bg-color);
  display: flex;
  height: 100vh;
  width: 100%;
  flex-direction: column;
  overflow: hidden;
}

.Nav {
  background-color: var(--nav-color);
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 15%;
  justify-content: space-between;
}

.NavLeft {
  display: flex;
  flex-direction: row;
  align-items: center;
  /* background-color: white; */
}

.NavRight {
  align-items: center;
  display: flex;
  flex-direction: row;
}

.Text {
  color: white;
  font-family: 'Inter', sans-serif;
  margin-right: 50px;
  font-size: 20px;
  animation: var(--fade-anim);
}

.reactLogo {
  height: 100px;
  width: 100px;
  transition: all;
  animation: var(--fade-anim), rotate 3s infinite linear;
}

.pageTitle {
  color: #61DAFB;
  font-size: 24px;
  font-family: 'Inter', sans-serif;
  opacity: 0;
  animation: var(--fade-anim);
}

.mainContent {
  display: flex;
  height: 85%;
  margin-left: 50px;
  flex-direction: row;
  justify-content: space-between;
  width: 120%;

}

.Title {
  color: white;
  font-family: 'Inter', sans-serif;
  font-size: 40px;
  opacity: 0;
  animation: fade 1s 1.5s forwards;
}

.list {
  list-style-image: url('./circle.png');
  /* opacity: 0; */
}

.listItem {
  color: white;
  font-family: 'Inter', sans-serif;
  font-size: 24px;
  opacity: 0;
  margin-top: 20px;
}

.reactjslogo {
  margin-left: 100px;
  opacity: 0;
  animation: fade 1s 1s forwards, rotate 10s infinite linear;
}

.first {
  animation: fade 1s 2s forwards
}

.second {
  animation: fade 1s 2.3s forwards
}

.third {
  animation: fade 1s 2.6s forwards
}

.forth {
  animation: fade 1s 2.9s forwards
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}